<template>
  <div id="login">
    <div class="box">
      <el-input label="用户名" type="string" v-model="name" placeholder="请输入用户名"></el-input>
      <el-input label="密码" type="string" v-model="password" placeholder="请输入密码" show-password></el-input>
      <div class="button">
        <el-button @click.native="login">登录</el-button>
        <el-button @click.native="zhuce">注册</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      password: ""
    }
  },
  methods: {
      login() {
          var data = {
              name: this.name,
              password: this.password
          };
          data = this.$Qs.stringify(data);
          this.$sendData('/login',data).then((response) => {
              if(response.code == "04") {
                  this.$message({
                      message: '登录成功',
                      type: 'success'
                  });
                  sessionStorage.setItem('isLogin',"true");
                  this.$router.push('/index');
              }else if(response.code == "03"){
                this.$message({
                  message: '系统错误',
                  type: 'warning'
                });
              } else {
                  this.$message({
                      message: '账号或密码错误',
                      type: 'warning'
                  });
              }
          })
      },
      zhuce() {
          this.$router.push('/zhuce')
      }
  }
};
</script>

<style lang="less">
#login {
  padding-top: 200px;
  .box {
    width: 500px;
    height: 500px;
    margin: auto;
  }
  .button {
      width: 200px;
      margin: auto;
      margin-top: 20px;
  }
}
</style>